y to fill the grid in a more visually pleasing manner. A downside to Packery is that if a photo does not fit a gap in the grid, it will push the next available photo that can fit regardless if it’s next in the sort order or not. This is a concession to fill the grid width and make it look aesthetically pleasing. The vertical layout will present the photos in a list where the photo is larger and centred.")]),_c('p',{staticClass:"font-weight-normal py-1 px-3"},[_vm._v("A major goal was to be able to dynamically update the grid by filtering tags and sorting by date, title or shuffle. The user can have one active filter and sort at any one time. Other photo grid features include a search by title input, a mode to view the Packery or the Vertical layout in fullscreen and a click photo view where the user can view a larger photo and photo details (title, tags, date uploaded).")]),_c('p',{staticClass:"font-weight-normal py-1 px-3"},[_vm._v("The admin has access to additional functionality, this includes an admin form that can upload a new photo, add a new tag, delete a tag or update a tag. The photo grid and available tags will dynamically update based off actions, there is no need to reload the DOM (Document Object Model) after every action. If the admin is logged in, they will have a different view of the photo grid Vertical layout. A form is present to allow the admin to delete or update a photo. The admin components can be dynamically rendered depending if the admin is logged in or not.")])])],1),_c('b-row',{staticClass:"justify-content-md-center mt-4"},[_c('b-col',{attrs:{"cols":"12","md":"auto"}},[_c('b-img',{attrs:{"thumbnail":"","center":"","fluid":"","src":_vm.assets.isotope_masonry_image,"alt":"isotope_masonry"}}),_c('p',{staticClass:"text-center font-weight-light"},[_vm._v("Isotope masonry")])],1),_c('b-col',{attrs:{"cols":"12","md":"auto"}},[_c('b-img',{attrs:{"thumbnail":"","center":"","fluid":"","src":_vm.assets.isotope_packery_image,"alt":"isotope_packery"}}