inimal then Heroku is a no brainer. Other more complex competitors like Amazon Web Services (AWS) should only be considered if your traffic is high enough to merit it. Heroku is a cloud platform that helps you deploy and host your applications. It takes care of the “easy” configurations and integrates seamlessly with GitHub for automated deployment.")]),_c('p',{staticClass:"font-weight-normal py-1 px-3"},[_vm._v("If I make a change to the frontend and want to deploy the changes to Heroku I would do the following: 1) Run a production build to place the static assets in the server folder, these are JavaScript files, style sheets, images that don’t change and are presented to the user. 2) Commit the changes to GitHub. 3) Sync GitHub and Heroku and deploy automatically or manually. AWS S3 was used a dedicated file storage to hold photo uploads, this was done because Heroku’s file system is ephemeral – which means that any changes to the file system only lasts until its shut down or restarted. Each time the app is booted up with a clean copy of the file system from the most recent deploy, clearing any recent file uploads.")])])],1),_c('b-row',{staticClass:"justify-content-md-center mt-4"},[_c('b-col',{attrs:{"cols":"12","md":"auto"}},[_c('b-img',{attrs:{"thumbnail":"","center":"","fluid":"","src":_vm.assets.heroku_pricing_image,"alt":"heroku_pricing"}}),_c('p',{staticClass:"text-center font-weight-light"},[_vm._v("Heroku pricing")])],1),_c('b-col',{attrs:{"cols":"12","md":"auto"}},[_c('b-img',{attrs:{"thumbnail":"","center":"","fluid":"","src":_vm.assets.AWS_bucket_image,"alt":"AWS_bucket"}}),_c('p',{staticClass:"text-center font-weight-light"},[_vm._v("Amazong Web Services S3 Bucket containing photos")])],1),_c('b-col',{attrs:{"cols":"12","md":"auto"}},[_c('b-img',{attrs:{"thumbnail":"","center":"","fluid":"","src":_vm.assets.github_respository_image,"alt":"github_respository"}}),_c('p',{staticClass:"text-center font-weight-light"},[_vm._v("GitHub respository ready to b